<template>
  <div class="nearby">
    <h3 class="nearby__title">附近店铺</h3>
    <div class="nearby__item" v-for="item in nearbyList" :key="item._id">
      <img class="nearby__item__img" :src="item.imgUrl" alt="">
      <div class="nearby__content">
        <div class="nearby__content__title">{{ item.name }}</div>
        <div class="nearby__content__tags">
          <span class="nearby__content__tag">月售{{ item.sales }}</span>
          <span class="nearby__content__tag">起送¥{{ item.expressLimit }}</span>
          <span class="nearby__content__tag">基础运费¥{{ item.expressPrice }}</span>
        </div>
        <div class="nearby__content__highlight">{{ item.slogan }}</div>
      </div>
    </div>
  </div>
  <Toast v-if="toastInfo.show" :message="toastInfo.message"/>
</template>

<script>
import { reactive } from 'vue'
import { get } from '@/utils/request'
import Toast, { useToastEffect } from '@/components/Toast.vue'

export default {
  name: 'NearbyView',
  components: { Toast },
  setup () {
    const nearbyList = reactive([])
    const {
      toastInfo,
      showToast
    } = useToastEffect()
    const getNearByList = async () => {
      try {
        const result = await get('/api/shop/hot-list')
        if (result?.errno === 0) {
          nearbyList.push(...result.data)
          nearbyList.push(...result.data)
          nearbyList.push(...result.data)
        } else {
          showToast('数据获取失败')
        }
      } catch (e) {
        console.log(e)
        showToast('请求失败')
      }
    }
    getNearByList()
    return {
      nearbyList,
      toastInfo
    }
  }
}
</script>

<style lang="scss" scoped>
@import "../../style/variables";

.nearby {
  font-family: PingFangSC-Medium;
  color: $content-fontcolor;

  &__title {
    margin: .16rem 0 .02rem;
    font-size: .18rem;
    font-weight: normal;
  }

  &__item {
    display: flex;
    padding-top: .12rem;

    &__img {
      width: .56rem;
      height: .56rem;
      vertical-align: top;
    }
  }

  &__content {
    flex: 1;
    margin-left: .16rem;
    padding-bottom: .12rem;
    border-bottom: .01rem solid $content-bgColor;

    &__title {
      line-height: .22rem;
      font-size: .16rem;
    }

    &__tags {
      margin: .08rem 0;
      line-height: .18rem;
      font-size: .13rem;
    }

    &__tag {
      margin-right: .16rem
    }

    &__highlight {
      line-height: .18rem;
      font-size: .13rem;
      color: #E93B3B;
    }
  }
}
</style>
